"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { Menu, X, Globe } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import { useLanguage } from "@/context/LanguageContext"; // adjust path if your folder structure is different

export default function Header() {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const { language, toggleLanguage } = useLanguage();

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 100);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  useEffect(() => {
    if (isMenuOpen) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "";
    }
    return () => {
      document.body.style.overflow = "";
    };
  }, [isMenuOpen]);

  const navItems = [
    { key: "home", href: "/" },
    { key: "aboutUs", href: "/about" },
    { key: "newsletters", href: "/newsletters" },
    { key: "contactUs", href: "/contact" },
  ];

  return (
    <>
      <header
        className={`
          fixed top-0 left-0 right-0 z-50 transition-all duration-300
          ${isScrolled ? "bg-blue-950/95 backdrop-blur-md shadow-lg" : "bg-transparent"}
        `}
      >
        <div className="max-w-7xl mx-auto px-6 py-4 md:py-6 flex items-center justify-between">
          {/* Logo */}
          <Link
            href="/"
            className="flex items-center gap-4 md:gap-5 hover:scale-105 transition-transform duration-200"
          >
            <img
              src="/images/logos/logo.png"
              alt="Sibiya Funeral Services Logo"
              className="h-20 md:h-28 lg:h-32 w-auto object-contain drop-shadow-lg"
            />
            <div className="hidden md:block">
              <div
                className={`
                  text-2xl md:text-3xl font-bold drop-shadow-xl transition-colors duration-300
                  ${isScrolled ? "text-white" : "text-white"}
                `}
              >
                Sibiya Funeral Services
              </div>
              <div
                className={`
                  text-sm md:text-base drop-shadow-lg transition-colors duration-300
                  ${isScrolled ? "text-blue-100" : "text-white/90"}
                `}
              >
                Our Family Serving Yours
              </div>
            </div>
          </Link>

          {/* Desktop Nav + Language Toggle */}
          <div className="hidden md:flex items-center gap-10 lg:gap-12">
            <nav className="flex items-center gap-10 lg:gap-12">
              {navItems.map((item) => (
                <Link
                  key={item.key}
                  href={item.href}
                  className={`
                    font-medium text-lg transition drop-shadow-md
                    ${isScrolled ? "text-white hover:text-green-400" : "text-white hover:text-green-300"}
                  `}
                >
                  {useLanguage().t(item.key)}
                </Link>
              ))}
            </nav>

            {/* Language Toggle Button */}
            <button
              onClick={toggleLanguage}
              className={`
                flex items-center gap-2 px-3 py-1.5 rounded-full text-sm font-medium transition-all
                ${isScrolled ? "bg-white/20 hover:bg-white/30 text-white" : "bg-white/10 hover:bg-white/20 text-white"}
              `}
              aria-label="Toggle language"
            >
              <Globe size={18} />
              <span className="font-bold">{language.toUpperCase()}</span>
            </button>
          </div>

          {/* Mobile Hamburger */}
          <button
            className="md:hidden text-white focus:outline-none"
            onClick={() => setIsMenuOpen(true)}
            aria-label="Open menu"
          >
            <Menu size={36} className="drop-shadow-md" />
          </button>
        </div>
      </header>

      {/* Mobile Drawer */}
      <AnimatePresence>
        {isMenuOpen && (
          <>
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 0.6 }}
              exit={{ opacity: 0 }}
              transition={{ duration: 0.3 }}
              className="fixed inset-0 bg-black z-40 md:hidden"
              onClick={() => setIsMenuOpen(false)}
            />

            <motion.div
              initial={{ x: "100%" }}
              animate={{ x: 0 }}
              exit={{ x: "100%" }}
              transition={{ type: "spring", damping: 25, stiffness: 300 }}
              className="fixed top-0 right-0 h-full w-4/5 max-w-sm bg-blue-950 text-white z-50 md:hidden shadow-2xl"
            >
              <div className="flex flex-col h-full">
                <div className="flex items-center justify-between p-6 border-b border-blue-800">
                  <div className="text-xl font-bold">{useLanguage().t("menu") || "Menu"}</div>
                  <button
                    onClick={() => setIsMenuOpen(false)}
                    className="text-white hover:text-green-400 focus:outline-none"
                    aria-label="Close menu"
                  >
                    <X size={32} />
                  </button>
                </div>

                <nav className="flex flex-col p-6 space-y-6 text-lg">
                  {navItems.map((item) => (
                    <Link
                      key={item.key}
                      href={item.href}
                      className="hover:text-green-400 transition-colors py-2"
                      onClick={() => setIsMenuOpen(false)}
                    >
                      {useLanguage().t(item.key)}
                    </Link>
                  ))}
                </nav>

                <div className="mt-auto p-6 border-t border-blue-800 text-sm text-blue-200">
                  <p>24/7 Emergency: 072 700 1800</p>
                </div>
              </div>
            </motion.div>
          </>
        )}
      </AnimatePresence>
    </>
  );
}